import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '@/firebase/firebase';
import { useState, useEffect } from 'react';

const OrderHistory = ({ userId }) => {
  const [orders, setOrders] = useState([]);

  useEffect(() => {
    const fetchOrders = async () => {
      const ordersCol = collection(db, 'orders');
      const q = query(ordersCol, where('userId', '==', userId));
      const snapshot = await getDocs(q);
      const ordersData = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setOrders(ordersData);
    };

    fetchOrders();
  }, [userId]);

  if (orders.length === 0) return <p>Kh√¥ng c√≥ ƒë∆°n h√†ng n√†o.</p>;

  return (
    <div className="space-y-3">
      {orders.map(order => (
        <div key={order.id} className="p-4 border rounded">
          <p><strong>M√£ ƒë∆°n h√†ng:</strong> {order.id}</p>
          <p><strong>Ng√†y ƒë·∫∑t:</strong> {new Date(order.createdAt).toLocaleDateString()}</p>
          <p><strong>T·ªïng ti·ªÅn:</strong> {order.total.toLocaleString()}‚Ç´</p>
        </div>
      ))}
    </div>
  );
};

export default OrderHistory;

